.navbar {
    .top_header {
        ul {
            list-style: none;
            text-align: center;
            background-color: var(--secondary-color);
            color: var(--white);
            margin: 0;
            padding: 5px;
        }
    }

    .navbar-container {
        display: flex;
        max-width: 100%;
        padding-top: 15px;
        padding-bottom: 15px;
        background-color: var(--primary-color);

        .logo {
            max-width: 150px;
            width: 100%;

            @media (max-width: 767px) {
                max-width: 100%;
                text-align: center;
            }

            a {
                color: var(--white);
                text-decoration: none;
                font-size: 20px;
                font-weight: 700;
                line-height: 100%;
            }

        }

        .menu_icon {
            display: none;

            @media (max-width: 767px) {
                display: block;
                position: fixed;
                left: 25px;
            }

            svg {
                path {
                    color: var(--white);
                    stroke: var(--white);
                }
            }
        }

        .menu.active {
            top: 90px;
            z-index: 99;

        }

        .menu {
            width: 100%;

            @media (max-width: 767px) {
                position: fixed;
                top: 110%;
                left: 0;
                width: 100%;
                height: 100%;
                -webkit-transition: all .6s cubic-bezier(.47, .5, 0, .95);
                -moz-transition: all .6s cubic-bezier(.47, .5, 0, .95);
                -ms-transition: all .6s cubic-bezier(.47, .5, 0, .95);
                -o-transition: all .6s cubic-bezier(.47, .5, 0, .95);
                transition: all .6s cubic-bezier(.47, .5, 0, .95);

            }

            nav {
                display: flex;
                justify-content: center;
                width: 100%;

                @media (max-width: 767px) {

                    display: block;
                    background-color: var(--white);
                    height: 100%;

                }

                ul {
                    display: flex;
                    list-style: none;
                    width: 100%;
                    justify-content: center;

                    @media (max-width: 767px) {
                        display: block;
                        ;
                    }
                    li.dropdown_menu.nav-link:hover>.dropdown_menus
                    {
                        height: auto;
                        opacity: 1;
                        visibility: visible;
                     
                    }
                 
                    li.dropdown_menu.nav-link  {
                        position: relative;
                       
                        .dropdown_menus {
                            position: absolute;
                            display: block;
                            background: var(--primary-color);
                            z-index: 9;
                            min-width: 150px;
                            top: 25px;
                            left: 0;
                            height: 0;
                            opacity: 0;
                            visibility: hidden;
                            transition: all 0.3s ease;
@media (max-width: 767px){
    background: var(--white);
    position: relative;
    top:0;

}
                            li {
                                padding: 10px;
                            }
                        }
                     
                    }  
                  

                    li {
                        margin: 0 10px;

                        @media (max-width: 767px) {
                            padding: 10px 15px;
                        }

                        a {
                            text-decoration: none;
                            color: var(--white);

                            @media (max-width: 767px) {
                                color: var(--primary-color);
                            }
                        }
                    }
                }

                ul.right-menu {
                    display: flex;
                    justify-content: flex-end;
                    max-width: 90px;
                    width: 100%;

                    @media (max-width: 767px) {
                        display: block;
                        max-width: 100%;
                    }

                    li {
                        svg {
                            font-size: 25px;
                        }
                    }

                    .search {
                        display: flex;

                        input {
                            background-color: transparent;
                            border: none;
                            border-bottom: 1px solid var(--white);
                            color: var(--white);
                            width: 0;
                            -webkit-transition: all .6s cubic-bezier(.47, .5, 0, .95);
                            -moz-transition: all .6s cubic-bezier(.47, .5, 0, .95);
                            -ms-transition: all .6s cubic-bezier(.47, .5, 0, .95);
                            -o-transition: all .6s cubic-bezier(.47, .5, 0, .95);
                            transition: all .6s cubic-bezier(.47, .5, 0, .95);

                            @media (max-width: 767px) {
                                color: var(--primary-color);
                            }

                            &::placeholder {
                                color: var(--white);

                                @media (max-width: 767px) {
                                    color: var(--primary-color);
                                }
                            }

                        }

                        input.input_active {
                            width: 100px;

                            @media (max-width: 767px) {
                                width: 100%;
                                border-color: var(--secondary-color);
                            }

                        }
                    }
                }
            }
        }
    }

}