.singleProduct {
    display: flex;
    justify-content: space-between;
    padding: 50px 0;
    .singleProduct_left img {
        max-width: 500px;
    }
    .singleProduct_left {
        max-width: 50%;
    }
    .singleProduct_right {
        max-width: 50%;
        h1
        {
            color: grey;
        }
        h2
        {
            font-weight: 300;
        }
        p.lead {
            font-weight: bold;
            font-size: 19px;
            margin: 10px 0;
        }
        p.price {
            font-size: 20px;
            font-weight: bold;
        }
        h3{
            color: grey;
            font-weight: 300;
            margin: 10px 0;
            line-height: 30px;
        }
        ::first-letter {
            text-transform: uppercase;
        }
        .cart
        {
            margin: 15px 0;
            button{
                background-color: var(--primary-color);
                color: var(--white);
                padding: 10px;
                border-radius: 5px;
                transition: all 0.3s linear;
                cursor: pointer;
            }
            :hover
            {
                background-color: var(--white);
                color: var(--primary-color);
            }
            button.go{
                margin-left: 20px;
            }
        }

    }
}
