.heading{
    text-align: center;
    margin: 30px 0;
}
.filters {
    .button {
        text-align: center;
        margin-bottom: 20px ;
        width: 100%;
        button.btn.btn {
            margin: 0 10px;
            padding: 10px;
            background: var(--primary-color);
            color: var(--white);
            border-radius: 5px;
            cursor: pointer;
        }
    }
    .card {
        max-width: calc(33% - 30px);
        display: inline-block;
        margin: 0 15px 30px;
        text-align: center;
        
        img{
            width: 100%;
            max-height: 350px;
            object-fit:contain;
        }
        p{
            font-weight: bold;
            font-size: 30px;
        }
        a
        {
            text-decoration: none;
            border: 1px solid var(--primary-color);
            padding: 10px;
            display: inline-block;
            margin-top: 10px;
            font-weight: 600;
            font-size: 15px;
            border-radius: 6px;
            color: var(--primary-color);

        }
    }
