.cat_products
{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 30px;
        grid-row-gap: 30px;
        text-align: center;
    
    .cat_data{
        img{
            width: 100%;
        }
        h1{
            font-size: 20px;
        }
        p.price
        {
            font-weight: bold;
            margin: 10px 0; 
            font-size: 20px;
        }
    }

}