.banner {
    position: relative;

    .banner_text {
        max-width: 50%;
    }
    .banner_text {
        max-width: 50%;
        position: absolute;
        top: 50%;
        left: 50px;
        transform: translateY(-50%);
        @media (max-width: 767px){
            max-width: 100%;
        }
        h2 {
            font-size: 16px;
            margin: 10px 0;
        }
        a {
           text-decoration: none;
           background-color: var(--primary-color);
           color: var(--white);
           padding: 10px 20px;
           display: inline-block;
           margin-top: 20px;

        }
    }
    .banner_img {
        img {
            width: 100%;
            height: 90vh;
            object-fit: cover;
            @media (max-width: 767px){
                height: 80vh;
            }
        }
    }
}
