.cate_card {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    column-gap: 20px;
    row-gap: 20px;
    text-align: center;
    img{
        width: 100%;
        min-width: 250px;
        max-width: 250px;
        
    }
    .cat_inner
    {
        a
        {
            text-decoration: none;
            color: var(--primary-color);
        }
    }
   
}

.cat_btn {
    text-align: center;
    margin: 50px 0;
    display: inline-block;
    width: 100%;
    Link {
        padding: 10px 20px;
        border: none;
        background: #000;
        color: #fff;
        border-radius: 5px;
        font-size: 15px;
        font-weight: bold;
    }
}